.typeProduct{
  min-height: 100vh;
}
@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}


.nameTypeProduct  {
  font-family: neon;
  color: #FB4264;
  font-weight: 700;
  text-shadow: 0 0 3vw #F40A35;
  font-size: large;
  text-align: center;
  font-size: 9em;
  width: 100%;
  height:300px ;
  background-color: rgb(4, 20, 20);
  display: flex;
  align-items: center;
  justify-content: center;
}


.nameTypeProduct {
  animation: nameTypeProduct 3s ease infinite;
  -moz-animation: nameTypeProduct 3s ease infinite;
  -webkit-animation: nameTypeProduct 3s ease infinite;
}
@media only screen and (max-width:850px){
  .nameTypeProduct{
    /* //display: none; */
    height: 200px;
   font-size: 4rem;
   
   
  }
}
@media only screen and (max-width:445px){
  .nameTypeProduct{
    /* //display: none; */
    height: 200px;
   font-size: 3rem;
   
   
  }
}

@keyframes nameTypeProduct {
  0%,
  100% {
    text-shadow: 0 0 1vw #FA1C16, 0 0 1vw #FA1C16, 0 0 5vw #FA1C16, 0 0 5vw #FA1C16, 0 0 .4vw #FED128, .1vw .1vw .1vw #806914;
    color: #FED128;
  }
  50% {
    text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 1vw #800E0B, 0 0 3vw #800E0B, 0 0 .2vw #800E0B, .1vw 2px .1vw #40340A;
    color:teal;
  }
}
.sortTypeProduct{
  margin-left: 70%;
  margin-top: 10px;
}
.sp{
  margin-left: 27px;
  font-size: xx-large;
  font-family: sans-serif;
  font-weight: 600;
 
}
