.wrapSPNB{
  width: 100%;
  max-width: 100%;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.xemthemSPNB{
   text-decoration: none;
 
}
.productNoiBatCss{
  width: 1000px;
}
.CardsanphamNoiBatCss{
  margin-top:8px;
  margin-left:20px;
  width:180px;
  position:relative;
  height: 250px;
  word-break: break-all;
  display: flex;
  flex-direction: column;
}
@media (max-width:533px){
  .CardsanphamNoiBatCss{
    margin-top:8px;
    margin-left:0px;
    width:160px;
    
    height: 250px;
    
  }
}
  @media (max-width:450px){
    .CardsanphamNoiBatCss{
     
      width:140px;
      height: 230px;
   
    }
    .CardsanphamNoiBatCss span{
       font-size: x-small;
    }

  }
  @media (max-width:400px){
    .CardsanphamNoiBatCss{
     
      width:120px;
      height: 200px;
   
    }
    .CardsanphamNoiBatCss span{
       font-size: xx-small;
    }
    .CardsanphamNoiBatCss p{
      font-size: xx-small;
   }

  }
  @media (max-width:360px){
    .CardsanphamNoiBatCss{
     
      width:100px;
      height: 180px;
   
    }
    .CardsanphamNoiBatCss span{
       font-size: xx-small;
    }
    .CardsanphamNoiBatCss p{
      font-size: xx-small;
   }

  }