.cardProductCss{
  margin-top:8px;
  margin-left: 20px;
  width:200px;
  position:relative;
  height: 260px;
  word-break: break-all;
  transition: transform ease-in 0.4s;
  margin-top: 20px;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;
}
.MuiCard-root{
transition: transform ease-in 0.4s;
}
.MuiCard-root:hover{
  transform: scale(1.05);
 

}
