.optionHeader{
  width: 100%;
  height: 50px;
  background-color: black;
  text-align: center;
  padding: 10px;
 
}
.linkOptionHeader{
  text-decoration: none;
  color: white;
  font-size: large;
  font-weight: 700;
  margin-left: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  cursor: pointer;
}

.linkOptionHeader:hover{
  color: teal;
}
.linkOptionHeader{
  transition: all ease-in 0.3s;
}

@media (max-width:600px){
  .linkOptionHeader{
    font-size: 13px;
  }
  .optionHeader{
    height: 40px;
  }
}

.optionHeader > span {
  color: aliceblue;
  font-size: large;
  font-weight: 700;
  margin-left: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  cursor: pointer;
}